@import url('https://fonts.googleapis.com/css2?family=Cardo:ital,wght@0,400;0,700;1,400&family=Lora:ital,wght@1,400;1,500&family=Montserrat:wght@300;400;600&display=swap');

/* COLORS */
$black: #222;
$darkGray:#444;
$gray: #ccc;
$lightGray: #eee;
$white:#fff;

$smoke:#fdfcfc;
$linen:#edeae8;
$sand: #eae3db;

$azure:#aae6f2;
$sage:#93bfb5;
$oregano:#abae95;

$success:rgb(84, 205, 50);
$error: darkred;
$darkRed:$error;


/* FONTS */
$heading: 'Montserrat', sans-serif;
$base: 'Cardo', serif;
$special: 'Lora', serif;


/* BUTTONS */
$btn-base-color:$darkGray;
$btn-text:$smoke;
$btn-hover:#000;

@mixin boxShadow(){
    -webkit-box-shadow: 0px 13px 25px -13px rgba(0,0,0,0.21);
    -moz-box-shadow: 0px 13px 25px -13px rgba(0,0,0,0.21);
    box-shadow: 0px 13px 25px -13px rgba(0,0,0,0.21);   
}
